/* Login.css */

/* Glassmorphic gradient background */
.login-page {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.15); /* Glassmorphic gradient background */
    backdrop-filter: blur(10px);
}

/* Container for login form */
.login-container {
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.2); /* Semi-transparent white background */
    border-radius: 10px;
    box-shadow: 0 8px 32px rgba(31, 38, 135, 0.37); /* Soft shadow */
    max-width: 400px; /* Set maximum width for larger screens */
    width: 90%; /* Set width for smaller screens */
    text-align: center; /* Center text */
}

/* Logo styling */
.app-logo-small {
    width: 100px; /* Adjust size as needed */
    height: auto; /* Maintain aspect ratio */
}

/* Form styling */
.form-footer {
    display: flex;
    justify-content: space-between; /* Place items at opposite ends */
    margin-top: 20px;
}

/* Link styling */
.form-footer a {
    color: #1890ff; /* Blue color */
}

/* Login button styling */
.form-footer button {
    width: 100px; /* Set width for the button */
}

/* Media queries for responsiveness */
@media screen and (max-width: 2560px) {
    .login-container {
        max-width: 70%; /* Adjust maximum width for large screens */
    }

    .app-logo-small {
        width: 350px; /* Adjust size for larger screens */
    }

    .login-container h1 {
        font-size: 36px; /* Adjust font size for larger screens */
    }
}

@media screen and (max-width: 1200px) {
    .login-container {
        max-width: 70%; /* Adjust maximum width for laptops */
    }

    .app-logo-small {
        width: 350px; /* Adjust size for smaller screens */
    }

    .login-container h1 {
        font-size: 36px; /* Adjust font size for smaller screens */
    }
}

@media screen and (max-width: 992px) {
    .login-container {
        max-width: 80%; /* Adjust maximum width for tablets */
    }

    .app-logo-small {
        width: 300px; /* Adjust size for smaller screens */
    }

    .login-container h1 {
        font-size: 32px; /* Adjust font size for smaller screens */
    }
}

@media screen and (max-width: 576px) {
    .login-container {
        max-width: 100%; /* Adjust maximum width for mobile phones */
    }

    .app-logo-small {
        width: 250px; /* Adjust size for smaller screens */
    }

    .login-container h1 {
        font-size: 28px; /* Adjust font size for smaller screens */
    }
}

@media screen and (max-width: 325px) {
    .login-container {
        max-width: 100%; /* Adjust maximum width for mobile phones */
    }

    .app-logo-small {
        width: 200px; /* Adjust size for smaller screens */
    }

    .login-container h1 {
        font-size: 24px; /* Adjust font size for smaller screens */
    }
}

