/* CSS for Analytics Component */

.card {
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .card-header {
    font-weight: bold;
    margin-bottom: 10px;
    font-size: 15px; /* Default font size */
  }
  
  .card-body {
    margin-bottom: 15px;
  }
  
  .progress1 {
    display: flex;
    justify-content: space-around;
    margin-top: 15px;
  }
  
  .column {
    width: 50%;
    
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    
    
  }
  
  .transaction-info
  {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    column-gap: 25px;
  }
  .card
  {
    width: 100%;
  }
  
  @media only screen and (max-width: 576px) {
    .card-header {
      font-size: 14px; /* Smaller font size for small screens */
    }
    .transaction-info {
        display: flex;
        flex-direction: column;
    }
    .row {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: space-between;
    }
    .column{
        width: 100%;
    }
    h5
    {
        font-size: 16px;
    }
  }
  
  @media only screen and (min-width: 576px) {
    .transaction-info {
        display: flex;
        flex-direction: row;
    }
  }
  
