/* HomePage.css */

.content {
  margin-top: 20px;
  padding: 0 20px; /* Add padding to the left and right */
}

.list-icon.active,
.chart-icon.active {
  color: blue; /* Change to the color you want for active icons */
}

/* Container Styles */
.filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.filter-group {
  display: flex;
  align-items: center;
}

.filter-group h6 {
  margin-right: 10px;
}

.add-new {
  margin-bottom: 10px;
}

.add-new-btn {
  background-color: #1890ff;
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}

.add-new-btn:hover {
  background-color: #40a9ff;
}

.icon-row {
  display: flex;
  justify-content: center;
}

.icon {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.list-icon,
.chart-icon {
  font-size: 24px;
  margin: 0 10px;
  cursor: pointer;
}

/* Table Styles */
.ant-table {
  background-color: #f5f5f5;
}

.ant-table-thead > tr > th {
  background-color: #1890ff;
  color: #fff;
}

.ant-table-tbody > tr > td {
  background-color: #fff;
  color: #000;
}

.ant-table-wrapper {
    overflow-x: auto; /* Enable horizontal scrolling */
  }
  
  .ant-table {
    min-width: 100%; /* Ensure the table takes up full width */
  }
  
  .ant-table-thead > tr > th,
  .ant-table-tbody > tr > td {
    white-space: nowrap; /* Prevent text from wrapping */
  }

  /* Add these styles to your CSS file */

/* Modal Form Styles */
.ant-modal-content {
    background-color: #f0f2f5; /* Set background color */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Add box shadow */
  }
  
  .actions
  {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 10px;
  }
  
  
  /* Save Button Styles */
  .add-transaction-button button {
    background-color: #1890ff; /* Button background color */
    color: #fff; /* Button text color */
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease; /* Smooth transition */
  }
  
  .add-transaction-button button:hover {
    background-color: #40a9ff; /* Button background color on hover */
  }
  

/* Responsive Styles */
@media screen and (max-width: 1440px) {
  .filters {
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 425px) {
  .filters {
    flex-direction: column;
    align-items: flex-start;
  }

  .filter-group {
    margin-bottom: 10px;
  }

  .filter-group h6 {
    margin-right: 0;
    margin-bottom: 5px;
  }

  .icon {
    margin-top: 10px;
  }
}

@media screen and (max-width: 320px) {
  .icon > * {
    font-size: 20px;
  }
}
