/* Navbar.css */

.navbar {
    background-color: #ffffff;
    padding: 1rem 2rem;
    position: sticky; /* Make navbar sticky */
    top: 0; /* Stick to the top of the viewport */
    z-index: 1000; /* Ensure navbar is above other content */
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-logo {
    height: 75px; /* Adjust the height of your logo */
}

.menu {
    display: flex;
}

.navbar-menu {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0; /* Reset margin */
}

.navbar-item {
    margin-left: 1rem;
    font-size: 20px;
}

.logout-button {
    background-color: #f44336; /* Red color */
    color: #ffffff; /* White text color */
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    cursor: pointer;
    font-size: 20px;
    transition: background-color 0.3s;
}

.logout-button:hover {
    background-color: #d32f2f; /* Darker red color on hover */
}

@media screen and (max-width: 768px) {
    .navbar {
        padding: 1rem;
    }

    .navbar-logo {
        height: 40px; /* Adjust the height of your logo for smaller screens */
    }

    .navbar-menu {
        flex-direction: row; /* Ensure menu items stay in a row */
        justify-content: flex-end; /* Align menu items to the right */
        margin-top: 1rem; /* Add margin to separate from logo */
    }

    .navbar-item {
        margin-left: 0.5rem; /* Adjust spacing between menu items */
        margin-right: 0.5rem; /* Adjust spacing between menu items */
    }
}

@media screen and (max-width: 550px) {
    .navbar {
        padding: 1rem;
    }

    .navbar-logo {
        height: 30px; /* Adjust the height of your logo for smaller screens */
    }

    .navbar-menu {
        flex-direction: row; /* Ensure menu items stay in a row */
        justify-content: flex-end; /* Align menu items to the right */
        margin-top: 1rem; /* Add margin to separate from logo */
    }

    .navbar-item {
        margin-left: 0.5rem; /* Adjust spacing between menu items */
        margin-right: 0.5rem; /* Adjust spacing between menu items */
        font-size: 15px
    }

    ..logout-button {
        font-size: 15px;
    }
}
